<template>
  <form @submit.prevent="updateNotice">
    <div class="form-group row">
      <label for="notice" class="col-sm-3 col-form-label">Text</label>
      <div class="col-sm-9">
        <textarea
          id="notice"
          v-model="form.text"
          class="form-control"
          :disabled="isUpdating"
          placeholder="এখানে নোটিশ লিখুন..."
        />
      </div>
    </div>
    <div class="form-group row">
      <label for="color" class="col-sm-3 col-form-label">Color</label>
      <div class="col-sm-9">
        <input
          type="color"
          id="color"
          v-model="form.color"
          class="form-control"
          :disabled="isUpdating"
        />
      </div>
    </div>

    <div class="form-group row">
      <div class="col-sm-9 offset-sm-3">
        <button
          type="submit"
          class="btn btn-success btn-block"
          :disabled="isUpdating || isLoading"
        >
          <div
            v-if="isLoading || isUpdating"
            class="spinner-border spinner-border-sm mb-2"
            role="status"
          >
            <span class="sr-only">Loading</span>
          </div>
          <span v-if="isLoading">Loading...</span>
          <span v-else-if="isUpdating">Updating...</span>
          <span v-else>Update</span>
        </button>
      </div>
    </div>
  </form>
</template>

<script>
import { firestore, Timestamp } from "../../../config/firebase";
import { mapState, mapActions } from "vuex";
export default {
  name: "NoticeFormComponent",
  computed: {
    ...mapState(["notice"])
  },
  watch: {
    notice: {
      handler: function(val) {
        if (val != null) {
          // set text
          this.form.text = val.text;
          // set color
          this.form.color = val.color;
        }
      },
      immediate: true
    }
  },
  data: () => ({
    form: {
      text: null,
      color: null
    },
    isUpdating: false,
    isLoading: false
  }),
  methods: {
    ...mapActions(["bindNotice"]),
    async updateNotice() {
      // set preloader
      this.isUpdating = true;

      const data = { ...this.form };
      // add current time
      data["updatedAt"] = Timestamp.now();

      console.log(data);

      try {
        // update data
        await firestore
          .collection("notices")
          .doc("latest")
          .set(data);
      } catch (e) {
        alert("Failed to update. Try again later");
      }

      // hide preloader
      this.isUpdating = false;
    },
    async initNotice() {
      // hide preloader
      this.isLoading = true;
      if (this.notice === null) {
        await this.bindNotice();
      }
      // show preloader
      this.isLoading = false;
    }
  },
  mounted() {
    this.initNotice();
  }
};
</script>

<style scoped></style>
